.valide {
    border: 2px solid black;
    background-color: var(--black-color);
    color: var(--white-color);
    width: 100px;
    height: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    box-sizing: border-box;
    font-size: 1.3rem;
    border-radius: 5px;
    margin: 10px 0;
    transition: all 300ms ease-in-out;
}
.valide:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.signUpLoginBoxQuiz {
    position: relative;
    
}
.connection-virus {
    height: 150px;
    position: absolute;
    top: 5%;
    left: 10%;
    -webkit-animation: anim-virus 10s linear normal backwards infinite;
            animation: anim-virus 10s linear normal backwards infinite;
}
@-webkit-keyframes anim-virus {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes anim-virus {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
.connection-virus-2 {
    height: 150px;
    position: absolute;
    bottom: 5%;
    right: 10%;
    animation: anim-virus 10s linear reverse backwards infinite;
}

