  letter-spacing: 6px;
  cursor: pointer;
  margin: 0 auto;
}

h1 a {
  color: inherit;
  text-decoration: none;
  font-family: 'Bangers', cursive;
}

h1 a:hover {
  color: inherit;
}

h2,
h3 {
  font-weight: normal;
  text-align: left;
  font-family: 'Roboto Condensed', sans-serif;
}

.bodyQuiz {
  margin: 0;
  padding: 0;
  background-image: url(/static/media/bg-noise-texture.78eacb5e.png);
  background-repeat: repeat;
  height: 100vh;
