  background-color: var(--red-color);
  background-image: url(/static/media/virus.0cbc4a7b.png);
  background-repeat: no-repeat;
  background-position: center center;
  display: flex;
  justify-content: space-between;
  height: 500px;
}

.startingImg {
  background-image: url(/static/media/virus2.0cbc4a7b.png);
}

